/* ==========================================================================
   WooCommerce Buttons responsive styles - begin
   ========================================================================== */

@include ipad-portrait {

    .woocommerce-page .qodef-content,
    div.woocommerce {
    
        a.button,
        a.added_to_cart,
        input[type="submit"],
        button[type="submit"]:not(.qodef-search-submit) {
            @include qodefButtonSize(small);
        }
    }
}
/* ==========================================================================
   WooCommerce Buttons responsive styles - end
   ========================================================================== */